body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.fullscreen-html {
  height: 101%;
  overflow: hidden;
  position: relative;
  width: 2000px;
  left: calc(100vw/2 - 1000px);  
}

.fullscreen {
  margin: 0;
  height: 100vh;
  overflow: hidden;
  position: relative;
  background: darkgray;
  text-align: center;
}

/* Disable double-tap-zoom on Safari/iOS */
button, body, video, div {
  touch-action: manipulation;
}

.mylist {
  max-width: 400px;
  max-height: calc(100vh - 5rem);
}
@media (max-width: 400px) {
  .mylist {
    max-height: unset;
    max-width: 95vw;
  }    
}
.mylistgroup {
  overflow-y: scroll;
  /* -webkit-overflow-scrolling: touch; */
}

.mylist-compact {
  max-height: calc(100vh - 5rem);
  /* TODO: detect mobile keyboard */
}


.mystencil {
  background: inherit;
  position: relative;
  left: 1.2em;
  top: -100%;
  height: 100%;
}
.mystencil-1 {
  left: .25em;
}
.mystencil-2 {
  left: .5em;
}
.mystencil-3 {
  left: .75em;
}
.mystencil-4 {
  left: 1em;
}
.mystencil-5 {
  left: 1.2em;
}
.mybars {
  background: white;
}

.my-bg-inherit {
  background: inherit;
}

.mypasswordfield {
  display: none;
}


