.tool-btn {
    font-size: inherit;
    color: rgba(255,255,255,0.5);
}

.tool-btn:active {
    color: rgba(255,255,255,0.7);
}

.flex-a:hover {
    color: white;
    text-decoration: none;
}

/* Remove dotted outline which Firefox adds to buttons and links after click. */
:focus {outline:none;}
::-moz-focus-inner {border:0;}

/* Firefox <= 63 doesn't support @media(hover:) query */
.flex-a:hover {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    color: white;
    text-decoration: none;
    /* box-shadow: 0 0 10px rgba(0,0,0,0.5); */
  }
  
  /* Mobile (Android/Chrome), touch. :active behaves like :hover.*/
  @media (hover) {
    .flex-a:hover {
        -webkit-transform: inherit;
                transform: inherit;
    }
    .flex-a:active {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
        color: inherit;
        text-decoration: none;
        box-shadow: 0 0 20px rgba(0,0,0,0.5);
    }
  }
  
  /* Mobile (iOS/Safari), touch. :active behaves like :hover.*/
  @media (hover: none) {
    .flex-a:hover {
        -webkit-transform: inherit;
                transform: inherit;
    }
    .flex-a:active {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
        /* color: inherit; */
        text-decoration: none;
        box-shadow: 0 0 20px rgba(0,0,0,0.5);
    }
  }
  
  /* Desktop, mouse pointer */
  @media (hover: hover) {
    .flex-a:hover {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
        /* color: white; */
        text-decoration: none;
        /* box-shadow: 0px 0px 10px rgb(0,0,0.5); */
        /* background: rgba(0,0,0,0.3); */
    }
  }
  
